.about-container{
    display: flex;
    background-color: white;
    color: black;
    height: 73vh;
    padding: 100px 0 100px 0;
}

.about-desc{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 55%;
    padding: 0 100px 0 100px;
}

.about-desc>h3{
    margin-bottom: 30px;
    font-size: 28px;
}

.about-desc>h4{
    font-size: 23px;
}

.about-desc>p{
    line-height: 1.5;
    font-size: 20px;
}
/*Imagen Logo escuela*/
.about-img{
    width: 45%;
    display: flex;
    align-self: center;
    overflow: hidden;
}