.footer{
    width: 100%;
    height: 100vh;
    background-color: black;
    color: white;
    padding-top: 100px;
    position: fixed;
    z-index: -2;
    bottom: 0;
    left: 0;
}

.footer-info{
    font-size: 35px;
    padding: 0px;
    width: 50%;
    margin: 5px auto;
}

.footer-info>h1{
    padding-bottom: 15px;
}

/* .footer-contact{
    font-size: 22px;
    padding: 100px 0 30px 50px;
    width: 70%;
    margin: 0 auto;
} */

.footer-sns{
    width: 95%;
    position: absolute;
    bottom: 100px;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
}

.sns-links{
    display: grid;
    width: 160%;
    margin: 0 auto;
    grid-template-columns: 1fr 1fr 1fr;
}

.facial,
.lista,
.facebook{
    color: plum;
    font-size: 20px;
    width: 300px;
    height: 300px;
    margin: 0 auto;
    padding-bottom: 10px;
    border-bottom:  4px solid transparent;
}

.facial:hover,
.lista:hover,
.facebook:hover{
    border-color: palevioletred;
    transition: all 0.3s ease-out;
}